  font-weight: 300;
  color: #1a1a1a;
}
.containerMain .info span {
  color: #000000;
  font-size: 12px;
}
.containerMain .info span a {
  color: #000000;
  text-decoration: none;
}
.containerMain .info span .fa {
  color: #fea91e;
}
